      .seventhBlock__map{
        position: relative;
        height: 500px;
        div{
          position: absolute;
          ul{
            position: relative;
            background: #fff;
            box-sizing: border-box;
            border-radius: 10px;
            box-shadow: 5px 5px 20px #fff, -5px -5px 20px #fff, 5px -5px  20px #fff, -5px 5px 20px #fff;
            li{
              &:nth-child(1){
                p{
                  font: 1.5em/1em "AdigianaUltra";
                  color: #ce2622;
                }
              }
              &:nth-child(2){
