                  color: #000;
                }
              }
              &:nth-child(3){
                p{
                  font: 1em/1em "HeliosCondC";
                  color: #000;
                }
              }
            }
            &:after{
              content: "";
              position: absolute;
              width: 0;
              height: 0;
              margin: 0 0 0 -.5em;
              box-sizing: border-box;
              border: 1em solid #fff;
              border-color: transparent transparent #fff #fff;
              border-radius: 5px;
              transform-origin: 0 0;
              box-shadow: -5px 5px 20px #fff;
            }
          }
        }
