            color: #bf172a;
          }
          input,
          select{
            width: 260px;
            font: 16px/.75em "HeliosCondC";
            color: #bf172a;
            border: 1px solid #bf172a;
            outline: none;
          }
        }
        button {
          margin: 25px 0 0;
